import Gallery from 'react-grid-gallery';
import Layout from '../components/Layout';

function getImgSize(imgSrc) {
  const newImg = new Image();

  newImg.onload = () => ({
    h: newImg.height,
    w: newImg.width
  });

  newImg.src = imgSrc; // this must be done AFTER setting onload
}

const buildGrid = () => {
  const grid = [];
  for (let i = 1; i <= 15; i++) {
    const img = `../static/img/services-gallery/${i}.jpg`;
    const { h, w } = getImgSize(img);

    grid.push({
      src: img,
      thumbnail: img,
      thumbnailWidth: w,
      thumbnailHeight: h
    });
  }

  return grid;
};

export default () => (
  <Layout>

    <section>
      <h1>Our Services</h1>
      <Gallery
        images={buildGrid()}
        enableImageSelection={false}
      />
    </section>

    <style jsx>
      {`
        section {
          flex: 1
          padding: 1rem
          text-align: center
        }
      `}
    </style>
  </Layout>
);



